/*JUMBOTRON*/
.jumbotron {
  height: 100vh; /*height to 100% of view height*/
  background: #054c61 url(../images/gradient-bkgrnd.png);
  background-repeat: no-repeat;
  background-size: cover;
  margin-bottom: 0;
  display: flex;
  flex-direction: column;
}

.jumbotron-text {
  /*TAGLINE UNDER NAME*/
  font-weight: 400;
  font-size: 2em;
  color: white;
  text-align: center;
  padding-top: 150px;
}

.jumbotron-text h1 {
  /*AUSTIN SCHROEDER*/
  font-weight: 800;
  font-size: 3em;
  margin-bottom: 0;
  user-select: none;
}

.jumbotron-text p {
  margin: 0;
  user-select: none;
}

/*SOCIAL LINKS*/
.ul-social {
  text-align: center;
  padding-left: 0;
}

.li-social-links {
  display: inline-block;
  list-style: none;
}

.social {
  font-size: 3em;
  color: white;
  padding: 20px;
}

.social:hover {
  text-shadow: 2px 2px 5px white;
}

/*ARROW LINK*/
#down-arrow {
  margin-top: auto;
  text-align: center;
  margin-bottom: 50px;
}

#down-arrow a {
  color: white;
}

#down-arrow:hover {
  text-shadow: 2px 2px 5px white;
}

/*CONTENT*/
.container {
  display: flex;
  flex-direction: column;
}

.bgflipped {
  /* background: url('../images/gradient-bkgrnd-flipped.png'); */
  background-repeat: no-repeat;
  background-size: cover;
}

.row {
  display: flex;
}

.white {
  background: white;
}

.side-content {
  flex: 0 0 16.6%;
}

.main-content {
  /* flex: 0 0 66.6%; */
  flex: 1;
  margin-top: 2rem;
  margin-bottom: 3rem;
}

.side-content-sm {
  flex: 0 0 10%;
}

.main-content-lg {
  flex: 0 0 80%;
  margin-top: 2rem;
  margin-bottom: 3rem;
}

.box-shadow {
  -webkit-box-shadow: 0px 0px 1px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 0px 1px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 0px 1px 0px rgba(0, 0, 0, 0.75);
}

h2 {
  font-weight: 200;
  font-size: 2rem;
  color: #054c61;
  text-transform: uppercase;
  text-align: center;
}

.content-body {
  line-height: 1.5;
  color: rgba(0, 0, 0, 0.75);
}

li {
  list-style: square;
}

/* EXPERIENCE */
.experience-flex {
  display: flex;
}

.audio-engineer {
  margin-bottom: 0;
}

.splide-container {
  width: 100%;
}

.knowcap-ul li {
  margin-bottom: 10px;
}

.gig-list {
  columns: 3;
  padding-left: 15px;
}

.gig-list li {
  margin: 0 5px;
}

